import React from "react"

class Popup extends React.Component {  
    render() {  
        return (  
            <div className='popup'>  
                    <h1>Hello,</h1>
                    <p> welcome to this sample [coursecode] test. We do hope that you are fully prepared to take this test.</p>  
                    <p>Kindly input your name below </p>
                    <p>Each test consists of 20 questions to be completed in 7 minutes</p>
                    <p>Endeavor to complete this test without any form of assistance</p>
                    <input 
                        onChange={this.props.changeName}
                        type="text"
                        placeholder="Name"
                    />
                    <br />
                    <button  className="start" onClick={this.props.startQuiz}>START</button>  
            </div>  
        );  
    }  
  }  
export default Popup