import React from "react"

class End extends React.Component {  
    render() {  
        return (  
            <div className='popup'>  
                    <h1>Dear {this.props.name}</h1>
                    <p>Thank you for completing this quiz</p>
                    <p>Your Score is: {this.props.score}</p>
                    <p>Please refresh the page if you'll like to take the test again</p>
            </div>  
        );  
    }  
  }  
export default End