import React from "react"

function Header(props){
    //pass state from app down here
    return(
        <div>
            <header className="header">
                <ul>
                    <li>{props.name}</li>
                    <li>course</li>
                    <li>{props.answered} /20</li>
                    <li>{props.minutes} : {props.seconds} </li>
                </ul>
            </header>
        </div>
    )
}


export default Header