import React from "react"

function QuestionItem(props){
    //pass state from app down here
    return(
        <div className="questionitem">
            <p>
                {props.item.id+1+". "}{props.item.question}
            </p>
            <label>
                <input 
                    type="radio"
                    name={props.item.id}
                    value={props.item.a}
                    onChange={props.handleOptionChange}
                />{props.item.a}
            </label>
            <br />
            <label>
                <input 
                    type="radio"
                    name={props.item.id}
                    value={props.item.b}
                    onChange={props.handleOptionChange}
                />{props.item.b}
            </label>
            <br />
            <label>
                <input 
                    type="radio"
                    name={props.item.id}
                    value={props.item.c}
                    onChange={props.handleOptionChange}
                />{props.item.c}
            </label>
            <br />
            <label>
                <input 
                    type="radio"
                    name={props.item.id}
                    value={props.item.d}
                    onChange={props.handleOptionChange}
                />{props.item.d}
            </label>
            <br />
            
        </div>
    )
}

export default QuestionItem