import React from 'react';
import './App.css';
import QuestionItem from './questionItem'
import questionsData from './questionsData'
import Finish from './finish'
import Popup from './popup'
import End from './End'
import Header from './header';

//this is the start of the app component
class App extends React.Component{
  constructor(){
    super()
    //state
    this.state = {
      data:questionsData,
      answer: questionsData.map(item => item.answer),
      selected: questionsData.map(item => item.selected),
      answered:0,
      showPopup: true,
      minutes: 7,
      seconds: 0,
      userName:"USER",
      finished:false,
      score:0,
    }
    this.handleOptionChange=this.handleOptionChange.bind(this)
    this.calculateScore=this.calculateScore.bind(this)
    this.startQuiz=this.startQuiz.bind(this)
    this.changeName=this.changeName.bind(this)

  }
  //this function is executed when the start button is clicked
  startQuiz(){
    //timer
    this.myInterval = setInterval(() => {
      const { seconds, minutes } = this.state

      if (seconds > 0) {
          this.setState(({ seconds }) => ({
              seconds: seconds - 1
          }))
      }
      if (seconds === 0) {
          if (minutes === 0) {
              this.calculateScore()
          } else {
              this.setState(({ minutes }) => ({
                  minutes: minutes - 1,
                  seconds: 59
              }))
          }
      } 
  }, 1000)
  //closes popup
  this.setState({  
    showPopup: !this.state.showPopup  
});
  }
  changeName(event){
    this.setState({
      userName: event.target.value.toUpperCase()
    })
  }
  handleOptionChange(event){
    let newSelected = this.state.selected
    var index = event.target.name
    if (index !== -1) {
      newSelected[index] = event.target.value;
    }
    this.setState({
      selected: newSelected
    })
    let reducer = (accumulator, item) => {
      if(!(item==="")){
       return accumulator+1
      }
      return accumulator 
    };

    let answered = this.state.selected.reduce(reducer,0)
    this.setState({
      answered:answered
    })

    console.log(this.state.answered)
  }
  calculateScore(){
    let score = 0
    this.setState({
      seconds:0,
      minutes:0
    })
    for(let i=0;i<this.state.answer.length;i++){
      if(this.state.answer[i]===this.state.selected[i]){
        score++
      }
    }
    this.setState({
      finished:true,
    })
    this.setState({
      score:score,
    })
    return score
  }
  
  render(){
    const questionItems = this.state.data.map(item => <QuestionItem  handleOptionChange={this.handleOptionChange} key = {item.id} item = {item}/>) 
    return(
      <div>
        <Header 
          name={this.state.userName}
          minutes={this.state.minutes} 
          seconds={this.state.seconds} 
          answered = {this.state.answered}
          className="header"
          />
        <br />
        <br />
        <br />
        
        <div className="questions">
          {this.state.showPopup ?  
            <Popup  
            text='Click "Close Button" to hide popup'  
            closePopup={this.togglePopup} 
            startQuiz={this.startQuiz} 
            changeName={this.changeName}
          />  
          : null  
          }
          {this.state.finished ?  
            <End  
              name = {this.state.userName}
              score= {this.state.score}
          />  
          : null  
          }
            
          {questionItems}
          <Finish calculateScore={this.calculateScore}/>
        </div>
      </div>
      
    )
  }
}

export default App;
